import * as THREE from 'three'

export const shaderMaterial = (
  uniforms: {
    [name: string]:
      | THREE.CubeTexture
      | THREE.Texture
      | Int32Array
      | Float32Array
      | THREE.Matrix4
      | THREE.Matrix3
      | THREE.Quaternion
      | THREE.Vector4
      | THREE.Vector3
      | THREE.Vector2
      | THREE.Color
      | number
      | boolean
      | Array<unknown>
      | null
  },
  vertexShader: string,
  fragmentShader: string,
  onInit?: (material?: THREE.ShaderMaterial) => void
) => {
  const material = class ShaderMaterial extends THREE.ShaderMaterial {
    public key: string = ''

    constructor(parameters = {}) {
      const entries = Object.entries(uniforms)
      let clonedUniforms = {}

      for (const [name, value] of entries) {
        const uniform = THREE.UniformsUtils.clone({ [name]: { value } })
        clonedUniforms = { ...clonedUniforms, ...uniform }
      }

      // Create unforms and shaders
      super({
        uniforms: clonedUniforms,
        vertexShader,
        fragmentShader,
      })

      for (const [name] of entries) {
        Object.defineProperty(this, name, {
          get: () => this.uniforms[name]!.value,
          set: (v) => (this.uniforms[name]!.value = v),
        })
      }

      // Assign parameters, this might include uniforms
      Object.assign(this, parameters)

      if (onInit !== undefined) onInit(this)
    }
  } as unknown as typeof THREE.ShaderMaterial & { key: string }

  material.key = THREE.MathUtils.generateUUID()

  return material
}
